.content-wrapper section {
    -webkit-filter: blur(10px);
    -moz-filter: blur(10px);
    -o-filter: blur(10px);
    -ms-filter: blur(10px);
    filter: blur(10px);
}

.content-wrapper {
    margin:auto;
    padding:0 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.content-wrapper>img {
    width:100%;
}

.registration-page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;

  background-color: white;
  padding: 30px;
}

.registration-page form {
  margin: 0;
}

input.register-form {
	padding: 5px;
	margin: 5px;
}

input[type=submit].register-form {
	padding: 10px;
	color: #EFFAEE;
	text-transform: uppercase;
	background-color: #F45F55;
	border: 1px solid #dfdfdf;
}


